#cic_data_modal {
  max-width: 95%
}

#cic_data_modal:focus {
  outline: none;
}

#cic_data_modal .nav-link.active:hover { color: #fff;
}

#cic_data_modal .btn-faq,
  #cic_data_modal .btn-toggle-min-max {
    position: absolute;
    width: 27px;
}

#cic_data_modal .btn-faq { right: 32px;
}

#cic_data_modal .btn-toggle-min-max { right: 0;
}

#cic_data_modal .modal-body   { height: 85vh;
}

#cic_data_modal .tab-content  { height: 95%;
}

#cic_data_modal .tab-pane {
    height: 100%;
    overflow-y: scroll;
}

#cic_data_modal .view-tab {
    overflow-y: visible;
}

#cic_data_modal .table-wrapper {
    height: calc(100% - 38px);
    margin-top: 21px;
    overflow-y: scroll;
}

.cic-data-table {
  word-break: break-word;
}

.cic-data-table caption { caption-side: top; }

.cic-data-table p { margin-bottom: 0; }

.cic-data-table thead th,
  .cic-data-table td {
    vertical-align: middle
  }

.cic-data-table thead th:nth-child(3), .cic-data-table td:nth-child(3) {
  width: 50%;
}

.search-field {
  font-size: 14.4px;
  font-size: 0.9rem
}

.search-field:focus {
  -webkit-box-shadow: none;
          box-shadow: none;
}



