/* Color system */

:export {
  terminalPrimary: #afe3d7;
}

.terminal {
  min-height: 100vh;
}

.terminal p {
    line-height: 22.4px;
    line-height: 1.4rem;
    margin-bottom: 0;
  }

.terminal__input {
    background: none;
    border: none;
    color: transparent;
    -webkit-box-flex: 1;
        -ms-flex-positive: 1;
            flex-grow: 1;
    height: 22.4px;
    height: 1.4rem;
    margin-top: -1px;
    text-shadow: 0 0 0 #afe3d7
  }

.terminal__input:focus {
  outline: none;
}

.terminal__input:focus + .terminal__caret span {
        -webkit-animation: blinker 0.7s cubic-bezier(0.42, 0, 0.58, 1) infinite alternate;
                animation: blinker 0.7s cubic-bezier(0.42, 0, 0.58, 1) infinite alternate;
}

.terminal__caret {
    padding-bottom: 3px;
  }

.terminal__caret > span {
      border-bottom: 2px solid #afe3d7;
    }

@-webkit-keyframes blinker {
  50% { opacity: 0; }
}

@keyframes blinker {
  50% { opacity: 0; }
}



